@charset "UTF-8";
@import 'reset.css';
.container {
  margin: 0 auto;
  padding-left: var(--gap);
  padding-right: var(--gap);
  max-width: var(--xl);
}

.container.relative {
  position: relative;
}

.gutter-xxl {
  padding-left: var(--gutter);
  padding-right: var(--gutter);
}

@media (max-width: 1660px) {
  .gutter-xxl {
    padding-left: var(--gap-sm);
    padding-right: var(--gap-sm);
  }
}
.header {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  z-index: 1000;
  height: 10rem;
  transition: all 500ms;
}

.header--container {
  pointer-events: initial;
  user-select: initial;
  will-change: auto;
  overflow: hidden;
  transition: all 500ms;
  position: relative;
  background-color: transparent;
  padding-bottom: 0;
  height: 100%;
  z-index: 5;
}

.header--wrap {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 10rem;
  padding-right: max(7.8vw, 13rem);
  padding-left: max(7.8vw, 13rem);
}

@media (max-width: 1660px) {
  .header--wrap {
    padding-left: var(--gap-md);
    padding-right: var(--gap-md);
  }
}
.header .brand {
  width: 148px;
  height: 44px;
  background: url("../images/common/logo-white.png") no-repeat 0 0;
}

.header .brand a {
  display: block;
  width: 100%;
  height: 100%;
}

.header nav {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 10rem;
}

.header .gnb {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  position: relative;
}

.header .gnb--li1 {
  z-index: 2;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.header .gnb--li1::after {
  content: "";
  opacity: 0;
  visibility: hidden;
  position: absolute;
  background: #0a5298;
  height: 38rem;
  top: 0rem;
  min-width: 169px;
  transition: all 500ms;
}

.header .gnb--li1 .menuitem {
  color: #fff;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.5s;
  padding: 0 2.75rem;
  font-size: 1.8rem;
  white-space: nowrap;
  min-width: 100px;
  position: relative;
  z-index: 3;
}

.header .gnb--li1 .menuitem span {
  display: inline-block;
  position: relative;
  line-height: 1;
  vertical-align: top;
}

.header .gnb--li1 .menuitem span::after {
  content: "";
  display: block;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: -11px;
  height: 2px;
  width: 0%;
  transition: all 0.25s;
  background: linear-gradient(to right, #147bb6, #4e978f, #85b348);
}

.header .gnb--li1:hover .menuitem span {
  color: #fff;
}

.header .gnb--li1:hover .menuitem span::after {
  width: 100%;
}

.header .gnb--li1:hover .depth {
  border: 0;
}

.header .gnb--li1:hover::after {
  opacity: 1;
  visibility: visible;
  height: 38rem;
  z-index: 2;
}

.header .gnb .depth {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  z-index: 3;
  padding: 2rem 0;
  height: 28rem;
  border-right: 1px solid #e5e5e5;
}

.header .gnb .depth li {
  line-height: 1;
}

.header .gnb .depth li a {
  text-align: center;
  display: block;
  color: #999;
  font-weight: 500;
  font-size: 1.6rem;
  padding: 1.05rem 0;
}

.header .gnb .depth li a:hover, .header .gnb .depth li a:focus {
  color: #fff;
}

.header .util {
  display: flex;
  align-items: center;
  gap: 0 2.3rem;
}

.header .util .special-links a {
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 25px;
  border: 1px solid rgba(255, 255, 255, 0.5);
  padding: 0.5rem 2.3rem;
  gap: 0 0.7rem;
}

.header .util .special-links span {
  display: block;
  color: #fff;
  font-size: 1.6rem;
}

.header .util .special-links i {
  display: block;
  width: 0.9rem;
  height: 0.9rem;
  background: url("../images/common/util-arrow-white.png") no-repeat;
}

.header .util .lang a {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0 0.8rem;
}

.header .util .lang a span {
  color: #fff;
  font-size: 1.6rem;
}

.header .util .lang a i {
  display: block;
  width: 2rem;
  height: 2rem;
  background: url("../images/common/lang-white.png") no-repeat;
}

