.recruit-step {
  display: flex;
  justify-content: center;
  gap: 10rem;
  margin-top: 5rem;
}
.recruit-step li {
  border: 1px solid #c2c2c2;
  position: relative;
  width: 20rem;
  height: 20rem;
  text-align: center;
  border-radius: 50%;
  font-size: 2.1rem;
  font-weight: bold;
  background: #fff;
}
.recruit-step li:before {
  position: absolute;
  right: -35%;
  top: 50%;
  transform: translate(-35%, -50%);
  display: block;
  content: "";
  width: 1.8rem;
  height: 3.4rem;
  background-image: url("../images/recruit/step-arrow.png");
}
.recruit-step li:last-child::before {
  display: none;
}
.recruit-step li > div {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 100%;
  transform: translate(-50%, -50%);
}
.recruit-step li span {
  display: block;
  font-size: 1.5rem;
  color: #a3a3a3;
}

.recruit-resume {
